<template>
  <section>
    <article
      v-for="(item, article) in articles"
      :key="article"
      @click="shareData(article)"
    >
      <img :src="item.screen" :title="item.title" :alt="item.title" />
      <div>
        <h2>{{ item.title }}</h2>
        <p>{{ item.stackMaster }}</p>
      </div>
    </article>
  </section>
</template>
<script>
export default {
  name: "Lab",
  data() {
    return {
      articles: [
        {
          title: "Breaking Bad Tributo",
          stackMaster: 'Glide.js',
          detail: ['Glide.js' , 'CSS-Grid' , 'Sass' , 'Gulp (compress images)'],
          description: `Página motivacional a testar o Glide.js, um plugin Javascript super leve (23kb) para criação de 
          slider de imagens. Outros pontos é que não tem dependências e dá suporte a mobile.`,
          url: "https://renato7oliveira.github.io/portfolio/Breaking_Bad_Tributo/src/",
          screen: require("../assets/images/portfolio-breaking-bad-tributo.jpg"),
        },
        {
          title: "Scroll Behavior simplificado",
          stackMaster: 'CSS',
          detail: ['CSS' , 'HTML'],
          description: `2 linhas é o suficiente para este efeito interessante.`,
          url: "https://renato7oliveira.github.io/portfolio/Scroll_Smooth/src/",
          screen: require("../assets/images/portfolio-scroll-behavior.jpg"),
        },
        {
          title: "Beatles Tributo",
          stackMaster: 'CSS3 Animation',
          detail: ['CSS3 Animation' , 'Vue.js Component' , 'Photoshop'],
          description: `
          A ideia principal foi criar animações com CSS3 puro. No header há um PNG transparente
          sobreposto a animação, na sequência foi criado uma grid fracionada e um componente de modal com Vue.js.
          `,
          url: "https://renato7oliveira.github.io/portfolio/Beatles_Tributo/src/",
          screen: require("../assets/images/portfolio-beatles.jpg"),
        },
        {
          title: "Street Fighter Tributo (old school)",
          stackMaster: 'Adobe Edge Animate',
          detail: ['Adobe Edge Animate' , 'Photoshop'],
          description: `
          Projeto interativo feito com Adobe Edge (R.I.P). 
          Após o usuário escolher um personagem, sua música tema é tocada 
          e aparecem botões que emitem suas vozes. O projeto ficou um pouco pesado devido um vídeo de abertura, 
          mas vale conferir. As imagens foram retiradas do Pinterest.
          `,
          url: "https://renato7oliveira.github.io/street-fighter-audio-project",
          screen: require("../assets/images/portfolio-street-fighter.jpg"),
        },
      ],
    };
  },
  methods: {
    shareData(keyDetail) {
      this.$router.push({
        name: "PortfolioDetalhe",
        params: { data: this.articles[keyDetail] },
      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>


<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 20px;
  article {
    transition: 0.3s;
    &:hover {
      cursor: pointer;
      transform: translateY(-5px);
      
    }
    &:active {
      transform: translateY(5px);
    }
    div{
      padding: 10px 15px;
    h2 {
      font-size: 0.8em;
      font-weight: bold;
    }
    p{
      font-size: 0.8em;
      color: #999;
    }
    }
    img {
      width: 100%;
      border-radius: 15px 15px 0 0;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
  }
}
/* MEDIA QUERIES START */
@media (max-width: 1024px) {
  section {
    grid-template-columns: 1fr;
    margin-bottom: 90px;
  }
}
/* MEDIA QUERIES END  */
</style>